import Block from './abstract/Block';

export default class ActivitiesBlock extends Block {
  constructor(e, d) {
    super();
    this.content = this.mapContents(e.content),
    this.title = e.title;
    if (d === 'mobile') return this.mobileRender()
    else return this.render()
  }
  mapContents(e) {
    return e.map((item) => {
      return {
        'type': 'li',
        'children': [
          {'type': 'p',
          'children': [item.content]}
        ]
      }
    });
  }
  mobileRender() {
    {
      return super.render({
        'type': 'article',
        'props': {
          'class': 'block',
          'id': 'activities'
        },
        'children': [
          {
            type: 'button',
            props: {
              'class': 'title',
              'onclick': 'javascript:' + super.toggleTitle('#activities')
            },
            'children': [this.title]
          }, {
            'type': 'ul',
            'props': {
              'class': 'toggle-container'
            },
            'children': this.content
          }
        ]
      })
    }
  }
  render() {
    return super.render({
      'type': 'article',
      'props': {
        'class': 'block',
        'id': 'activities'
      },
      'children': [
        {
          'type': 'h1',
          'props': {
            'class': 'title'
          },
          'children': [this.title]
        }, {
          'type': 'ul',
          'children': this.content
        }
      ]
    })
  }
}
