import Block from './abstract/Block';

export default class EducationBlock extends Block {
  constructor(e, d) {
    super();
    this.content = this.mapContents(e.content),
    this.title = e.title;
    if (d === 'mobile') return this.mobileRender()
    else return this.render()
  }
  mapContents(e) {
    return e.map((item) => (
      {
        'type': 'div',
        "children": [
          {
            'type': 'span',
            'props': {
              'class': 'institution'
            },
            'children': [item.institution]
          }, {
            'type': 'span',
            'props': {
              'class': 'period'
            },
            'children': [`${item.startDate} - ${item.endDate}`]
          }, {
            'type': 'p',
            'props': {
              'class': 'major'
            },
            'children': [`${item.degree} ${item.major}`]
          }
        ]
      }
    ))
  }
  mobileRender() {
    return super.render({
      'type': 'article',
      'props': {
        'class': 'block',
        'id': 'education'
      },
      'children': [
        {
          type: 'button',
          props: {
            'class': 'title',
            'onclick': 'javascript:' + super.toggleTitle('#education')
          },
          'children': [this.title]
        }, {
          'type': 'div',
          'props': {
            'class': 'toggle-container'
          },
          'children': this.content
        }
      ]
    })
  }
  render() {
    return super.render({
      'type': 'article',
      'props': {
        'class': 'block',
        'id': 'education'
      },
      'children': [
        {
          'type': 'h1',
          'props': {
            'class': 'title'
          },
          'children': [this.title]
        }, {
          'type': 'div',
          'children': this.content
        }
      ]
    })
  }
}
