import Block from './abstract/Block';

export default class KeywordsBlock extends Block {
  constructor(e, d) {
    super();
    this.content = e.content.map((item) => (
      {
        'type': 'li',
        'children': [{
          'type': 'span',
          'props': {
            'class': 'keyword-span'
          },
          'children': [item]
        }]
      }
    )),
    this.title = e.title;
    if (d === 'mobile') return this.mobileRender()
    else return this.render()
  }
  render() {
    return super.render({
      type: 'article',
      props: {
        'id': 'keywords',
        'class': 'block'
      },
      children: [
        {
          type: 'h1',
          props: {
            'class': 'title'
          },
          children: [this.title]
        },
        {
          'type': 'ul',
          'props': {
            'class': 'keyword-ul'
          },
          'children': [
            ...this.content
          ]
        }
      ]
    });
  }
  toggleTitle() {
  }
  mobileRender() {
    return super.render({
      type: 'article',
      props: {
        'id': 'keywords',
        'class': 'block'
      },
      children: [
        {
          type: 'button',
          props: {
            'class': 'title',
            'onclick': this.toggleTitle
          },
          children: [this.title]
        },
        {
          'type': 'ul',
          'props': {
            'class': 'keyword-ul'
          },
          'children': [
            ...this.content
          ]
        }
      ]
    });
  }
}
