import Block from './abstract/Block';

export default class ContactBlock extends Block {
  constructor(e, d, ...name) {
    super();
    this.name = `${name[0]} ${name[1]}`,
    this.title = e.title,
    this.content = this.mapContents(e.content);
    if (d === 'mobile') return this.mobileRender()
    else return this.render()
  }
  mapContents(e) {
    return Object.keys(e).map((item) => {
      var p = document.createElement('p');
      switch(item) {
        case 'birthday':
          return {
            'type': 'p',
            'children': [`DOB: ${e[item]}`]
          }
        case 'phone':
          return {
            'type': 'p',
            'children': [`M: ${e[item]}`]
          }
        case 'email':
          return {
            'type': 'p',
            'props': {
              'class': 'email'
            },
            'children': [`${e[item]}`]
          }
        default:
          return {
            'type': 'p',
            'children': [`${e[item]}`]
          }
      }
    });
  }
  mobileRender() {
    return super.render({
      'type': 'article',
      'props': {
        'class': 'block',
        'id': 'contact'
      },
      'children': [
        {
          type: 'button',
          props: {
            'class': 'title active',
            'onclick': 'javascript:' + super.toggleTitle('#contact')
          },
          'children': [this.title]
        }, {
          type: 'div',
          props: {
            'class': 'toggle-container show'
          },
          children: [
            {
              'type': 'p',
              'children': [this.name]
            },
            ...this.content
          ]
        }
      ]
    })
  }
  render() {
    return super.render({
      'type': 'article',
      'props': {
        'class': 'block',
        'id': 'contact'
      },
      'children': [
        {
          'type': 'h1',
          'props': {
            'class': 'title'
          },
          'children': [this.title]
        }, {
          'type': 'p',
          'children': [this.name]
        },
        ...this.content
      ]
    })
  }
}
