import Block from "./abstract/Block";

export default class SummaryBlock extends Block {
  constructor(e, s, d) {
    super();
    (this.content = e.content),
      (this.title = e.title),
      (this.keywords = e.content.map(item => ({
        type: "li",
        children: [
          {
            type: "span",
            props: {
              class: "keyword-span"
            },
            children: [item]
          }
        ]
      })));
    if (d === "mobile") return this.mobileRender();
    else return this.render();
  }
  mobileRender() {
    return super.render({
      type: "article",
      props: {
        id: "summary",
        class: "block"
      },
      children: [
        {
          type: "button",
          props: {
            class: "title active",
            onclick: "javascript:" + super.toggleTitle("#summary")
          },
          children: [this.title]
        },
        {
          type: "p",
          props: {
            class: "toggle-container show"
          },
          children: [
            this.content,
            {
              type: "ul",
              props: {
                class: "keyword-ul"
              },
              children: [
                {
                  type: "li",
                  props: {
                    class: "label"
                  },
                  children: ["Keywords:"]
                },
                ...this.keywords
              ]
            }
          ]
        }
      ]
    });
  }
  render() {
    return super.render({
      type: "article",
      props: {
        id: "summary",
        class: "block"
      },
      children: [
        {
          type: "h1",
          props: {
            class: "title"
          },
          children: [this.title]
        },
        {
          type: "p",
          props: {},
          children: [...this.content]
        }
      ]
    });
  }
}
