export default class Block {
  toggleTitle(elem) {
    return `
      var elem = document.querySelector('${elem} .toggle-container');
      var title = document.querySelector('${elem} .title');
      elem.classList.contains('show') ?
      elem.classList.remove('show') :
      elem.classList.add('show');
      title.classList.contains('active') ?
      title.classList.remove('active') :
      title.classList.add('active');
    `;
  }
  createElement(node) {
    if (typeof node === 'string') {
      return document.createTextNode(node);
    }
    const $elem = document.createElement(node.type);
    node.props && Object.keys(node.props).map((prop, key) => {
      $elem.setAttribute(prop, node.props[prop])
    });
    node.children.map((child) => (
      this.createElement(child)
    )).forEach((childNode) => {
      $elem.appendChild(childNode)
    })
    return $elem;
  }
  render(block) {
    return this.createElement(block);
  }
}
