import Block from './abstract/Block';

export default class ExperienceBlock extends Block {
  constructor(e, d) {
    super();
    this.content = this.mapContents(e.content),
    this.title = e.title;
    if (d === 'mobile') return this.mobileRender()
    else return this.render()
  }
  mapContents(e) {
    return e.map((item, key) => {
      return {
        'type': 'div',
        'children': [
          {
            'type': 'p',
            'props': {
              'class': 'organization'
            },
            'children': [item.organization]
          },
          {
            'type': 'span',
            'props': {
              'class': 'role'
            },
            'children': [item.role]
          },
          {
            'type': 'span',
            'props': {
              'class': 'period'
            },
            'children': [`${item.startDate} - ${item.endDate.length !== 0 ? item.endDate : 'Present'}`]
          },
          {
            'type': 'p',
            'props': {
              'class': 'summary'
            },
            'children': [item.summary ? item.summary : '']
          },
          {
            'type': 'ul',
            'props': {
              'class': 'description'
            },
            'children': item.description ? this.getDescription(item.description) : []
          }
        ]
      }
    })
  }
  getDescription(obj) {
    return Object.keys(obj).map((item) => {
      return {
        'type': 'li',
        'children': [
          {
            'type': 'p',
            'children': [obj[item]]
          }
        ]
      }
    })
  }
  mobileRender() {
    return super.render({
      'type': 'article',
      'props': {
        'id': 'experience',
        'class': 'block'
      },
      'children': [
        {
          type: 'button',
          props: {
            'class': 'title',
            'onclick': 'javascript:' + super.toggleTitle("#experience")
          },
          'children': [this.title]
        }, {
          'type': 'ul',
          'props': {
            'class': 'experience-ul toggle-container'
          },
          'children': this.content
        }
      ]
    });
  }
  render() {
    return super.render({
      'type': 'article',
      'props': {
        'id': 'experience',
        'class': 'block'
      },
      'children': [
        {
          'type': 'h1',
          'props': {
            'class': 'title'
          },
          'children': [this.title]
        }, {
          'type': 'ul',
          'props': {
            'class': 'experience-ul'
          },
          'children': this.content
        }
      ]
    });
  }
}
