import Block from './abstract/Block';

export default class HeaderBlock extends Block {
  constructor(...name) {
    super();
    this.name = `${name[0]} ${name[1]}`;
    return this.render()
  }
  render() {
    return super.render({
      'type': 'h1',
      'props': {
        'id': 'header'
      },
      children: [this.name]
    });
  }
}
