import Block from './abstract/Block';
import HeaderBlock from './HeaderBlock';
import EducationBlock from './EducationBlock';
import SummaryBlock from './SummaryBlock';
import KeywordsBlock from './KeywordsBlock';
import ActivitiesBlock from './ActivitiesBlock';
import ExperienceBlock from './ExperienceBlock';
import ContactBlock from './ContactBlock';
import Strings from './model/strings';

export default class App {
  constructor() {
    this.main = document.getElementById('main');
    this.aside = document.getElementById('left-container');
    this.app = document.getElementById('root');
    this.device = undefined;
    return this.init();
  }
  init() {
    this.device = this.getDevice();
    this.app.classList.add(this.device);
    window.onresize = () => this.handleWindowResize();
    this.headerBlock = new HeaderBlock(Strings.firstName, Strings.lastName, this.device);
    this.educationBlock = new EducationBlock(Strings.education, this.device);
    this.summaryBlock = new SummaryBlock(Strings.summary, this.device);
    this.keywordsBlock = new KeywordsBlock(Strings.keywords, this.device);
    this.activitiesBlock = new ActivitiesBlock(Strings.activities, this.device);
    this.experienceBlock = new ExperienceBlock(Strings.experience, this.device);
    this.contactBlock = new ContactBlock(Strings.contact, this.device, Strings.firstName, Strings.lastName);
    return this.render();
  }
  getDevice() {
    if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
     return 'mobile';
   } else {
     return 'desktop';
   }
  }
  handleWindowResize() {
    this.device = this.getDevice();
  }
  render() {
    this.main.appendChild(this.headerBlock);
    this.main.appendChild(this.summaryBlock);
    this.main.appendChild(this.experienceBlock);
    this.main.appendChild(this.educationBlock);
    this.aside.appendChild(this.keywordsBlock);
    this.main.appendChild(this.activitiesBlock);
    this.main.appendChild(this.contactBlock);
  }
};
